import React from 'react';
import { styled } from 'styled-components';

const Inner = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
  .career {
    width: 450px;
  }
  .career div + div {
    margin-top: 50px;
  }

  .career h1 {
    font-size: 2.4rem;
    margin-bottom: 20px;
  }
  .career p {
    margin-top: 5px;
    font-family: "Gowun Batang", serif;
    font-weight: 400;
    font-style: normal;
    font-size: 1.2rem;
  }
  .career .date {
    padding-left: 25px;
  }

  .myStory {
    width: 800px;
    /* flex-grow: 0.7; */
    margin: 0 0 10px 20px;


    .strong {
      font-family: "Gowun Batang", serif;
      font-weight: 700;
      font-style: normal;
    }
    h1 {
      font-size: 2.4rem;
      margin-bottom: 20px;
    }

    h2 {
      display: flex;
      justify-content: center;
      font-size: 1.5rem;
      margin-bottom: 10px;
    }
    
    p {
      text-indent: 18px;
      justify-content: center;
      font-family: "Gowun Batang", serif;
      font-weight: 400;
      font-style: normal;
      font-size: 20px;
      padding: 10px ;
      line-height: 25px;
    }
  }
`;

function History() {
  return (
    <Inner className='mainInner'>
      <div className='career'>
        <div className='education'>
          <h1>Education</h1>
          <p> (MERN Stack)   </p>
          <p className='date'>- 2023.08 - 2024.01</p>
        </div>
        <div className='graduation'>
          <h1>Graduation</h1>
          <p>  /</p>
          <p className='date'>- 2015.03 - 2021.02 </p>
          <p> </p>
          <p className='date'>- 2012.03 - 2015.02 </p>
        </div>
      </div>
      <div className='myStory'>
        <h1>Introduce</h1>
        <h2 className='strong'>"   , IT   "</h2>
        <p>     . IT   ,          <strong className='strong'>Full-Stack </strong>    .</p>
        <p>        <strong className='strong'>'if'</strong>    , <strong className='strong'>Javascript</strong>        ''.</p>
        <p><strong className='strong'>Redux Store</strong>      <strong className='strong'>Node.js</strong> <strong className='strong'>Express</strong>   <strong className='strong'>MongoDB</strong>  <strong className='strong'>RESTful API</strong>     .</p>
        <p><strong className='strong'>Socket.io</strong>        <strong className='strong'>DB </strong>    .      <strong className='strong'>    </strong>.</p>
      </div>
    </Inner>
  );
}

export default History;