import React from 'react';
import { styled } from 'styled-components';
import MongModalSlide from '../slide/MongModalSlide';
import { FaGithub  } from "react-icons/fa6";

const ModalBg = styled.div`
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background: gray;
  background-color: rgba(0, 0, 0, 0.7);
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  `;

const ModalBox = styled.div`
  width: 90rem;
  height: 45rem;
  background-color: white;
  border-radius: 30px;
  padding: 50px 50px;
  
  .headerBox {
    position: relative;
    text-align: center;
    margin-bottom: 30px;
  }
  .headerBox .closeBtn {
    position: absolute;
    right: 0;
    font-size: 40px;
    cursor: pointer;
    transition: 1s;
    &:hover {
      transform: rotate(360deg);
      transition: 1s;
    }
  }

  .headerBox p {
    font-family: "Gowun Batang", serif;
    font-weight: 400;
    font-style: normal;
    font-size: 1.2rem;
  }

  .detailBox {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  .textBox .toolTextIcon {
    font-size: 20px;
  }

  .toolText.be {
    background: #bcbcbc69;
  }
  .textBox .toolText {
    display: inline-block;
    font-family: "Gowun Batang", serif;
    font-weight: 400;
    font-style: normal;
    border-radius: 8px;
    border: 1px solid gray;
    padding: 3px 4px;
    margin-left: 5px;
    margin-bottom: 5px;
  }
  
  .git {
    text-decoration: none;
    margin-top: 10px;
    background: black;
    color: white;
    border: none;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background: gray;
      transition: 0.3s;
    }
  }

  .summaryHeader {
    margin: 10px 0 20px 0 ;
  }
  .summaryTextBox {
    font-family: "Gowun Batang", serif;
    font-weight: 400;
    font-style: normal;
    padding-left: 10px;
    font-size: 1.1rem;
  }
  .summaryTextBox p + p {
    margin-top: 10px;
  }
  .summaryInfo span {
    line-height: 1.2rem;
    margin-top: 10px;
  }
  .summaryInfo span {
    display: inline-block;
  }


  
`;



function goniModal({ handleCloseMadal }) {
  return (
    <ModalBg>
      <ModalBox>
        <div className='headerBox'>
          <p className='closeBtn' onClick={() => handleCloseMadal()}></p>
          <h1>My Mong</h1>
          <p>       </p>
        </div>
        <div className='detailBox'>
          <div className='imgBox'>
            <MongModalSlide />
          </div>
          <div className='textBox'>
            <p>
              <span className='toolTextIcon'>:</span>
              <span className='toolText'>React</span>
              <span className='toolText'>Rudex</span>
              <span className='toolText'>Aioxs</span>
              <span className='toolText'>EJS</span>
              <span className='toolText'>styled-components</span>
              <span className='toolText'>react-router-dom</span>
              <span className='toolText be'>MongoDB</span>
              <span className='toolText be'>Node.js</span>
              <span className='toolText be'>Express</span>
              <span className='toolText be'>Passport</span>
              <span className='toolText be'>Socket.io</span>
              <span className='toolText be'>AWS S3</span>
            </p>
            <a href='https://github.com/zziimm/finalProject' target='_blank' rel='noreferrer noopener' className='toolText git'><FaGithub /> Github FE</a>
            <a href='https://github.com/zziimm/finalProjectServer' target='_blank' rel='noreferrer noopener' className='toolText git'><FaGithub /> Github BE</a>
            <p className='summaryHeader'>Summary</p>
            <div className='summaryTextBox'>
              <p> BackEnd  </p>
              <p>  : FrontEnd 4, BackEnd 2</p>
              <p className='summaryInfo'>
                <span>      ,    /  .</span>
                <span>      ,            .</span>
                <span> kakakoMap api       .</span>
                <span> Socket.io   1:1        .</span>
                <span>   8   BE MongoDB        pipeline .</span>
              </p>
            </div>
          </div>
        </div>
      </ModalBox>
    </ModalBg>
  );
}

export default goniModal;